<template>
  <v-container>

    <!-- タイトル -->
    <v-row>
      <v-sheet class="title">
        交通アクセス
      </v-sheet>
    </v-row>

    <v-row>
      <!-- アクセス情報 -->
      <v-col cols="12" sm="5">
        <!-- 所在地 -->
        <v-row class="contents-cat">
          <div>
            <span class="sub-title">所在地</span>
            <ul class="contents custom-list">
              <li>〒１９３－０８４５</li>
              <li>東京都八王子市初沢町１２３１－１７</li>
              <li>高尾力栄ビル５F ( エレベーター有 )</li>
            </ul>
          </div>
        </v-row>

        <!-- 電話 -->
        <v-row class="contents-cat">
          <div>
            <span class="sub-title">電話</span>
            <br />
            <span class="contents">
              <a :href="`tel:${phone.replace(/-/g, '')}`">{{ phone }}</a>
            </span>
          </div>
        </v-row>

        <!--  交通 -->
        <v-row class="contents-cat">
          <div>
            <span class="sub-title">交通</span>
            <ul class="contents custom-list">
              <li>【電車】</li>
              <ul class="contents custom-list">
                <li>　中央本線・京王高尾線</li>
                <li>　高尾駅(南口)より徒歩２分</li>
              </ul>
              <li>【車】</li>
              <ul class="contents custom-list">
                <li>　 近隣にコインパーキング有ります</li>
              </ul>
            </ul>
          </div>
        </v-row>
      </v-col>

      <!-- 地図 -->
      <v-col justify="center" cols="12" sm="7">
        <v-row class="contents-cat">
          <div class="access-map">
            <div>
              <img src="@/assets/takao_map.png" alt="Takao Map" class="map-img">
            </div>
          </div>
        </v-row>
      </v-col>
    </v-row>

    <!-- Google Map -->
    <v-row class="contents-cat">
      <v-col cols="12">
        <section ref="gmapSection">
          <v-sheet>
            <iframe
              v-if="mapLoaded"
              class="map-google"
              frameborder="0"
              style="border:0"
              referrerpolicy="no-referrer-when-downgrade"
              :src="mapSrc"
              allowfullscreen
            ></iframe>
          </v-sheet>
        </section>
      </v-col>
    </v-row>

  </v-container>
</template>

<script setup>
import { Loader } from '@googlemaps/js-api-loader';
import { ref, onMounted, onUnmounted } from 'vue';

const mapLoaded = ref(false);
const gmapSection = ref(null);
const mapSrc = ref(
  `https://www.google.com/maps/embed/v1/place?key=${import.meta.env.VITE_GOOGLE_MAPS_API_KEY}&q=${import.meta.env.VITE_CLINIC_ADDRESS}&zoom=15`
);

let observer;

onMounted(() => {
  observer = new IntersectionObserver(
    (entries) => {
      if (entries[0].isIntersecting && !mapLoaded.value) {
        mapLoaded.value = true;
        // 一度ロードしたら監視を解除
        observer.unobserve(gmapSection.value);
      }
    },
    {
      threshold: 0.5,
    }
  );

  if (gmapSection.value) {
    observer.observe(gmapSection.value);
  }
});

onUnmounted(() => {
  if (observer) {
    observer.disconnect();
  }
});
</script>

<script>
export default {
  name: "AccessInfo",
  computed: {
    phone() {
      return import.meta.env.VITE_CLINIC_PHONE;
    }
  },
};
</script>

